import React from 'react';
import { MuiThemeProvider } from '@material-ui/core';
import Theme from './Theme';
import Slideshow from './components/Slideshow';

class App extends React.Component {
  render() {
    return (
      <MuiThemeProvider theme={Theme}>
        <Slideshow />
      </MuiThemeProvider>
    );
  }
}

export default App;
